body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(145deg, #0a2640, #144b7d);
  color: white;
  min-height: 100vh;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 3rem;
  min-height: 100vh;
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  texte-align:center;
  zoom: 40%;
}

h1 {
  font-size:clamp(2.5rem, 8vw, 8rem);
  font-weight: bold;
  margin: 0rem 1rem;       
  white-space: nowrap;
  text-align: center;
  display: inline-block; 
  transform: translateX(-0px);
}

.subtitle {
  font-size: 3rem !important;
  font-style: italic;
  color: #cce3f0;
  white-space: nowrap;
  margin-top: 3rem;
  margin-bottom: 15rem;
  transform: translateX(0px);
  
}

.input-zone {				 
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  width: 100%;
}

.input-zone input {
  padding-left: 3rem;
  padding-right: 40rem;
  text-align: left !important;
  padding-top: 3rem;
  padding-bottom: 3rem;
  border-radius: 30px 0 0 30px;
  border: none;
  flex-grow: 1;
  font-size: 2rem;
  background-color: #111;
  color: white;
}

.input-zone .mic-button {
  padding: 3rem 3rem;
  border-radius: 0 30px 30px 0;
  border: none;
  background-color: #222;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  
}

.button-group {
  margin: 0.5rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: nowrap;
  width: 100%;
  max-width: 900px;   
  overflow-x: auto;
}

.button-group button {
  padding: 2rem 3rem;
  background-color: transparent;
  border: 2px solid #fff;
  color: white;
  border-radius: 12px;
  font-size: 1.6rem;
  cursor: pointer;
  transition: 0.3s;
  margin-bottom: 6rem;
  
}

.button-group button:hover {
  background-color: #0c4160;
}

.icon-row {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 6rem;
  flex-wrap: nowrap;
  width: 100%;          
  max-width: 1000px;        
}

.icon-row img {
  width: 300px;
  height: 300px;
  border-radius: 0%;
  padding: 0;
  object-fit: contain;
  box-shadow: 0 0 8px rgba(0,0,0,0.2); 
  cursor: pointer;      
  transition: transform 0.3s ease;
}

.icon-row img:hover {
  transform: scale(1.1);
  box-shadow: 0 0 15px 6px rgba(255, 255, 255, 0.8);
}

.icon-bar {
  display: flex;             
  flex-direction: row;      
  flex-wrap: nowrap;         
  justify-content: center;   
  align-items: center;     
  gap: 1.5rem;               
  margin-bottom: 6rem;      
}

.icon-fixed {
  width: 15rem;
  height: 15rem;
  object-fit: contain;
  cursor: default;
  margin-top: 15rem;          
}

.examples-bar {
  display: flex;
  align-items: center;
  gap: 1rem;            
  margin: 2rem 0;       
  flex-wrap: nowrap;     
  justify-content: center;
  font-size: 5rem;    
  color: #cce3f0;       
}

.examples-bar button {
  background: rgba(255,255,255,0.1);
  border: 1px solid #cce3f0;
  border-radius: 20px;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: background 0.2s;
  font-size: 1.8rem;
  color: white;
}

.examples-bar button:hover {
  background: rgba(255,255,255,0.2);
}

.chat-message.user {
  background-color: #2a77e6;     
  color: white;
  align-self: flex-end;               
  border-bottom-right-radius: 0;
}


.chat-message.diny {
  background-color: #0b2040;   
  color: white;
  align-self: flex-start;         
  border-bottom-left-radius: 0;  
}

/* Barre de saisie */
.input-zone.bottom-input {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 0 auto 2rem;
  padding: 0.75rem 1rem;
  background-color: #000;
  border-radius: 999px;
  box-shadow: 0 0 20px rgba(0,0,0,0.4);
  
}

/* Champ texte */
.input-zone.bottom-input input {
  flex: 1;                    
  padding: 1rem 1.82rem;
  font-size: 1.8rem;
  border: none;
  border-radius: 999px 0 0 999px;  
  background-color: transparent;
  color: white;
}

/* Bouton « Envoyer » */
.input-zone.bottom-input .mic-button {
  margin-left: 1rem;          
  padding: 1rem 2rem;
  font-size: 1.8rem;
  font-weight: bold;
  background-color: #2a77e6;
  border: none;
  border-radius: 0 999px 999px 0; 
  cursor: pointer;
}

.chat-wrapper {
  width: 90%;
  max-width: none;      
  margin: 1rem ;
  padding: 1rem;
  zoom: 50%;
  position: center;
 
}

.chat-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  margin-top: -10rem;
}

.chat-box {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 100%;
  padding: 1rem;
  background: #0d1b2a;
  border-radius: 10px;
  overflow-y: auto;
  overflow-x: hidden;
}

.chat-message {
  display: inline-block;
  max-width: 80%;
  padding: 0.8rem 2rem;
  white-space: normal;
  overflow-wrap: break-word;
  border-radius: 20px;
  word-break: break-word;
  margin-bottom: 0.5rem;
  font-size: 2.0rem
}

.chat-message.user {
  background-color: #2a77e6 !important;
  color: white !important;
  align-self: flex-end !important;
  border-bottom-right-radius: 0 !important;
}

.chat-message.diny {
  background-color: #0b2040 !important;
  color: white !important;
  align-self: flex-start !important;
  border-bottom-left-radius: 0 !important;
}



